.ipl-dash-board{
	display: grid;
	grid-template-columns: repeat(5,1fr);
	grid-template-rows: repeat(5,1fr);
	grid-row-gap: 25px;
	
	.s{
		grid-column: span 1;
	}
	
	.title-card{
		grid-column: span 3;
		
		.title{
			text-align: center;
			align-content: center;
			font-size: 3rem;
			color: red;
		}
		
		.author{
			text-align: center;
			align-content: center;
			color: greenyellow;
		}
	}
	
	.ss{
		grid-column: span 5;
	}
	
	.container {
            position: relative;
    }
	
	.live{
		display: flex;       /* Center the button content */
   		align-items: center; /* Vertically center */
    	justify-content: center; /* Horizontally center */
    	width: 100%;         /* Full width */
    	height: 100%;
    	font-size: 1.5rem;   /* Font size */
		background-color: rgb(75, 226, 139);
		color: rgb(255, 255, 255);
	}
	
	.tdb{
		display: flex;       /* Center the button content */
   		align-items: center; /* Vertically center */
    	justify-content: center; /* Horizontally center */
